<template>
  <div>
    <!-- Modal -->
    <modal
      :popup="showModal"
      :title="modalTitle"
      :cancel="+modalType !== 1"
      v-bind="{
        ...(+modalType === 2
          ? { classBody, }
          : {})
      }"
      :confirm-text="+modalType === 2 ? '儲存' : '確定'"
      @finish="closeMod()"
      @confirm="onConfirm"
    >
      <p
        v-if="+modalType < 2"
        v-text="modalMessage"
      />
      <div
        v-else
        class="modal-body"
      >
        <div
          v-for="item in inputList"
          :key="item.title"
          class="form-group row"
        >
          <label
            for="lname"
            class="col-sm-2 text-right control-label col-form-label"
            v-text="item.title"
          />
          <div class="col-sm-10">
            <input
              v-if="item.type === 'input'"
              v-model="item.value"
              type="text"
              class="form-control"
              :placeholder="item.placeholder"
            >
            <textarea
              v-else
              v-model="item.value"
              class="form-control"
              style="height:100px;"
              :placeholder="item.placeholder"
            />
          </div>
        </div>
        <li
          v-if="alertMessage"
          style="color: #F00"
          v-text="alertMessage"
        />
      </div>
    </modal>
    <!-- Modal -->

    <div class="page-wrapper">
      <div class="page-breadcrumb">
        <div class="row">
          <div class="col-12 d-flex no-block align-items-center">
            <h4 class="page-title">
              供應商
            </h4>
          </div>
        </div>
      </div>

      <div class="container-fluid">
        <buttonCont
          :style-data="styleData"
          @onButton="callModal($event)"
        />
        <data-table
          :titles="[
            '廠商名稱',
            '連絡人',
            '室內電話',
            '手機',
            '網址',
            '其他資訊',
          ]"
          :pagination="pagination"
          @onPage="onPage"
          @check="onAllCheck"
        >
          <tr
            v-for="({ Name, TaxID , ContactPerson, Tel, PhoneNumber, WebUrl , Status , Remark, Id }, key) in list"
            :key="key"
          >
            <td>
              <label class="customcheckbox m-b-20">
                <input
                  ref="checkbox"
                  type="checkbox"
                  @input="onCheck({ $event, Id, key })"
                >
                <span class="checkmark" />
              </label>
            </td>
            <td>
              <li>
                <a
                  href="javascript:void(0)"
                  class="product-spec"
                  v-text="Name"
                />
              </li>
              <li
                style="color: #acacac"
              >
                統一編號：<br>{{ TaxID || '無資料' }}
              </li>
              <li
                v-if="Status"
                style="color: #009688"
              >
                正常使用 <i class="m-r-10 mdi mdi-check" />
              </li>
              <li
                v-else
                style="color: #acacac"
              >
                停用
              </li>
            </td>
            <td v-text="ContactPerson" />
            <td v-text="Tel" />
            <td v-text="PhoneNumber" />
            <td v-text="WebUrl" />
            <td>
              <dropdown
                :title="'操作'"
                :items="[
                  { text: '修改', type: 'item', action: 'edit' },
                  { type: 'divider' },
                  { text: Status ? '停止使用' : '啟用', type: 'item', action: 'toggle' },
                  { text: '刪除', type: 'item', action: 'delete' },
                ]"
                @onMenu="onMenu({ ...$event, Id, Status, key })"
              />
              <hr>
              備註：<br>{{ Remark || '(無備註)' }}
            </td>
          </tr>
        </data-table>
      </div>
    </div>
    <div
      v-if="showModal"
      class="modal-backdrop fade show"
    />
  </div>
</template>

<script>
import dataTable from '@/components/dataTable.vue';
import dropdown from '@/components/dropdown.vue';
import modal from '@/components/modal/index.js';
import buttonCont from '@/components/buttonCont.vue';
import { mapActions, mapGetters } from 'vuex';

export default {
  name: 'Supplier',
  components: {
    dataTable,
    dropdown,
    modal,
    buttonCont,
  },
  data() {
    return {
      state: {
        showAdd: false,
        showSuccess: false,
      },
      // -- modal --
      showModal: false,
      modalTitle: '',
      modalMessage: '',
      modalType: 0, // 1: 只有確認 2: 填單
      // -- modal --
      alertMessage: '',
      classBody: {
        'modal-dialog': true,
        'modal-900': true,
      },
      checkedList: [],
      inputList: [
        { title: '*廠商名稱', placeholder: '請輸入', value: '', type: 'input' },
        { title: '統一編號', placeholder: '請輸入', value: '', type: 'input' },
        { title: '連絡人', placeholder: '請輸入', value: '', type: 'input' },
        { title: '室內電話', placeholder: '請輸入', value: '', type: 'input' },
        { title: '手機', placeholder: '請輸入', value: '', type: 'input' },
        { title: '地址', placeholder: '請輸入', value: '', type: 'input' },
        { title: 'Email', placeholder: '請輸入', value: '', type: 'input' },
        { title: '網址', placeholder: '請輸入', value: '', type: 'input' },
        { title: '備註', placeholder: '', value: '', type: 'textarea' },
      ],
      // -- Buttons --
      styleData: [
        {
          forBut: { color: 1, mdi: 1, text: '新增供應商' },
          forMod: { title: '新增供應商', type: '2', message: '1', action: 'onCreate' },
        },
        {
          forBut: { color: 2, mdi: 2, text: '正常使用' },
          forMod: { title: '', type: '', message: 2, action: 'onEnableAll' },
        },
        {
          forBut: { color: 2, mdi: 3, text: '停止使用' },
          forMod: { title: '', type: '', message: 3, action: 'onStopAll' },
        },
        {
          forBut: { color: 3, mdi: 4, text: '刪除' },
          forMod: { title: '', type: '', message: 4, action: 'onDeleteAll' },
        },
      ],
    };
  },
  computed: {
    ...mapGetters({
      list: 'Supplier/list',
      pagination: 'Supplier/pagination',
    }),
  },
  created() {

  },
  mounted() {
    console.log('debug: mounted -> window.cs = this', window.cs = this);
    this.getList({
      ...this.pagination,
      page: 1,
      PageLimit: 10,
    });
  },
  methods: {
    ...mapActions('Supplier', {
      getList: 'read',
      addSupplier: 'create',
      changeSetting: 'modify',
      deleteSupplier: 'deleted',
      changeStatus: 'update',
    }),
    ...mapActions('Modal', {
      createModal: 'create',
    }),
    callModal({ title, type, message, action }) {
      
      if (['onCreate', 'onEdit'].includes(action)) { 
        document.body.classList.add('modal-open');
        document.body.style.paddingRight = '17px';
        this.showModal = true;
        this.modalTitle = title || '系統提示';
        this.modalMessage = message || '';
        this.modalType = type || 0;
        this.action = action || 'closeMod';
        this.alertMessage = '';
      } else if (typeof this[action] === 'function') {
        this[action]();
        // onEnableAll
        // onStopAll
        // onDeleteAll
      }
    },
    closeMod() {
      document.body.classList.remove('modal-open');
      document.body.style.paddingRight = null;
      this.showModal = false;
      this.modalMessage = '';
      this.alertMessage = '';
    },
    onConfirm() {
      const action = this[this.action];
      if (typeof action === 'function') {
        action();
      }
    },
    onCreate() {
      const { inputList } = this;

      const Name = inputList[0].value;
      const TaxID = inputList[1].value;
      const ContactPerson = inputList[2].value;
      const Tel = inputList[3].value;
      const PhoneNumber = inputList[4].value;
      const Address = inputList[5].value;
      const Email = inputList[6].value;
      const WebUrl = inputList[7].value;
      const Remark = inputList[8].value;

      if (Name) {
        // if (true) {
        this.addSupplier({
          Name,
          TaxID,
          ContactPerson,
          Tel,
          PhoneNumber,
          Address,
          Email,
          WebUrl,
          Status: true,
          Remark,
        })
          .then((result) => {
            this.closeMod();
            this.createModal({
              message: '新增成功',
              showCancel: false,
            }).then(() => {});
            // this.modalMessage = '新增成功';
            this.getList({
              ...this.pagination,
            });
          })
          .catch((err) => {
            this.alertMessage = err.message || '不明錯誤';
          });
      } else {
        this.alertMessage = '所有星號(*)必填';
      }
    },
    onEdit() {
      const { inputList } = this;

      const Name = inputList[0].value;
      const TaxID = inputList[1].value;
      const ContactPerson = inputList[2].value;
      const Tel = inputList[3].value;
      const PhoneNumber = inputList[4].value;
      const Address = inputList[5].value;
      const Email = inputList[6].value;
      const WebUrl = inputList[7].value;
      const Remark = inputList[8].value;

      if (Name) {
        // if (true) {
        this.changeSetting({
          Id: this.modifyingId,
          data: {
            Name,
            TaxID,
            ContactPerson,
            Tel,
            PhoneNumber,
            Address,
            Email,
            WebUrl,
            Status: true,
            Remark,
          },
        })
          .then((result) => {
            this.closeMod();
            this.createModal({
              message: '修改成功',
              showCancel: false,
            }).then(() => {});
            // this.modalMessage = '新增成功';
            this.getList({
              ...this.pagination,
            });
          })
          .catch((err) => {
            this.alertMessage = err.message || '不明錯誤';
          });
      } else {
        this.alertMessage = '所有星號(*)必填';
      }
    },
    onMenu({ text, type, index, action, Id, Status, key }) {
      if (action === 'edit') {
        const { Name, TaxID, ContactPerson, Tel, PhoneNumber, Address, Email, WebUrl, Remark } = this.list[key];
        this.inputList[0].value = Name;
        this.inputList[1].value = TaxID;
        this.inputList[2].value = ContactPerson;
        this.inputList[3].value = Tel;
        this.inputList[4].value = PhoneNumber;
        this.inputList[5].value = Address;
        this.inputList[6].value = Email;
        this.inputList[7].value = WebUrl;
        this.inputList[8].value = Remark;
        this.callModal({
          message: '',
          title: '修改供應商資料',
          action: 'onEdit',
          type: 2,
        });
        this.modifyingId = Id;
      } else if (action === 'delete') {
        // 下拉選單刪除
        this.createModal({
          message: '確定刪除此項目？',
          // showCancel: false
        }).then(() => {
          this.deleteSupplier(Id)
            .then((result) => {
              this.createModal({
                message: '刪除成功',
                showCancel: false,
              }).then(() => {});
              this.getList({
                ...this.pagination,
              });
            })
            .catch((err) => {
              this.createModal({
                message: err.message || '不明錯誤',
                showCancel: false,
              }).then(() => {});
            });
        });
        // 下拉選單刪除
      } else if (action === 'toggle') {
        // 下拉選單 停用/啟用
        this.createModal({
          message: `確定${Status ? '停用' : '啟用'}此項目？`,
          // showCancel: false
        }).then(() => {
          this.changeStatus({
            Id,
            Status: !Status,
          })
            .then((result) => {
              this.createModal({
                message: '修改成功',
                showCancel: false,
              }).then(() => {});
              this.getList({
                ...this.pagination,
              });
            })
            .catch((err) => {
              this.createModal({
                message: err.message || '不明錯誤',
                showCancel: false,
              }).then(() => {});
            });
        });
        // 下拉選單 停用/啟用
      }
    },
    onCheck({ $event, Id, key }) {
      const { checked } = $event.target;
      console.log('debug: onCheck -> checked', checked);
      if (checked && !this.checkedList.includes(Id)) {
        this.checkedList.push(Id);
      }
      if (!checked && this.checkedList.includes(Id)) {
        const index = this.checkedList.indexOf(Id);
        // console.log('debug: onCheck -> index', index);
        if (index > -1) {
          this.checkedList.splice(index, 1);
        }
      }
    },
    onAllCheck(checked) {
      if (checked) {
        const { checkbox } = this.$refs;
        const list = Array.isArray(checkbox)
          ? checkbox
          : [checkbox];
        list.forEach((el) => {
          el.checked = true;
        });
        this.checkedList.splice(0);
        this.list.forEach(({ Id }) => {
          this.checkedList.push(Id);
        });
      } else {
        this.clearAllchecked();
      }
    },
    // onSeleteAll($event) {
    //   const { checked } = $event.target;
    //   if (checked) {
    //     const { checkbox } = this.$refs;
    //     const list = Array.isArray(checkbox)
    //       ? checkbox
    //       : [checkbox];
    //     list.forEach((el) => {
    //       el.checked = true;
    //     });
    //     this.checkedList.splice(0);
    //     this.list.forEach(({ Id }) => {
    //       this.checkedList.push(Id);
    //     });
    //   } else {
    //     this.clearAllchecked();
    //   }
    // },
    clearAllchecked() {
      const { checkbox } = this.$refs;
      const list = Array.isArray(checkbox)
        ? checkbox
        : [checkbox];
      list.forEach((el) => {
        el.checked = false;
      });
      this.checkedList.splice(0);
    },
    onEnableAll() {
      if (this.checkedList.length) {
        const promiseList = [];
        this.checkedList.forEach((Id) => {
          promiseList.push(this.changeStatus({
            Id,
            Status: true,
          }));
        });
        Promise.allSettled(promiseList)
          .then((result) => {
            // this.closeMod();
            this.createModal({
              message: '修改成功',
              showCancel: false,
            }).then(() => {});
            // this.modalMessage = '修改成功';
            this.getList({
              ...this.pagination,
            });
          })
          .catch((err) => {
            this.createModal({
              message: err.message || '不明錯誤',
              showCancel: false,
            }).then(() => {});
            // this.modalMessage = err.message || '不明錯誤';
          });
      }
    },
    onStopAll() {
      if (this.checkedList.length) {
        const promiseList = [];
        this.checkedList.forEach((Id) => {
          promiseList.push(this.changeStatus({
            Id,
            Status: false,
          }));
        });
        Promise.allSettled(promiseList)
          .then((result) => {
            // this.clearAllchecked();
            // this.closeMod();
            this.createModal({
              message: '修改成功',
              showCancel: false,
            }).then(() => {});
            // this.modalMessage = '修改成功';
            this.getList({
              ...this.pagination,
            });
          })
          .catch((err) => {
            // this.clearAllchecked();
            this.createModal({
              message: err.message || '不明錯誤',
              showCancel: false,
            }).then(() => {});
            // this.modalMessage = err.message || '不明錯誤';
          });
      }
    },
    onDeleteAll() {
      if (this.checkedList.length) {
        const promiseList = [];
        this.checkedList.forEach((Id) => {
          promiseList.push(this.deleteSupplier(Id));
        });
        Promise.allSettled(promiseList)
          .then((result) => {
            this.clearAllchecked();
            // this.closeMod();
            this.createModal({
              message: '刪除成功',
              showCancel: false,
            }).then(() => {});
            // this.modalMessage = '修改成功';
            this.getList({
              ...this.pagination,
            });
          })
          .catch((err) => {
            this.clearAllchecked();
            this.createModal({
              message: err.message || '不明錯誤',
              showCancel: false,
            }).then(() => {});
            // this.modalMessage = err.message || '不明錯誤';
          });
      }
    },
    onPage(Page) {
      if (+Page !== +this.pagination.Page) {
        this.getList({
          ...this.pagination,
          Page,
        });
        this.clearAllchecked();
      }
    },
  },
};

</script>

<style>
@import '~@css/css/supplier.css';
</style>
